@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.navigation {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 5% 0;
}

.navigation a {
  font-size: 3vw;
  font-weight: 700;
}

.navigation a + a {
  margin-left: 8%;
}

a {
  text-decoration: none;
  color: inherit;
}

.film-icon {
  text-align: center;
  margin-top: 15vh;
  font-size: 30vw;
  font-weight: 700;
}

.movie-detail {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 10% 5%;
  width: 90%;
}

.movie-detail__text {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-left: 5%;
  width: 60%;
}

.img-box {
  width: 40%;
}

.movie-detail__img {
  border-radius: 10%;
  box-shadow: 0px 0px 5px rgba(255, 255, 255, 0.8);
  width: 100%;
  height: auto;
}

.movie-detail__title-box {
  display: -webkit-flex;
  display: flex;
  width: 100%;
}

.movie-detail__title {
  font-size: 3vw;
  width: 80%;
  text-align: left;
  font-weight: 700;
}

.movie-detail__info {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 20%;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  -webkit-justify-content: center;
          justify-content: center;
}

.movie-detail__rating {
  font-size: 2.5vw;
  color: rgb(228, 187, 36);
  display: -webkit-flex;
  display: flex;
}

.fa-star {
  margin-right: 5px;
}

.movie-detail__runtime {
  font-size: 2.2vw;
  opacity: 0.5;
  margin-top: 3px;
  text-align: right;
}

.movie-detail__summary {
  font-size: 2vw;
  opacity: 0.8;
  width: 100%;
  margin-top: 10%;
}

body {
  background-color: black;
  color: white;
  font-family: "Roboto", sans-serif;
  margin-top: 20px;
}

.movies {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.movie {
  width: 20%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 10px;
}

.movie-container {
  width: 80%;
}

.movie img {
  border-radius: 10%;
  box-shadow: 0px 0px 5px rgba(255, 255, 255, 0.8);
  width: 100%;
}

.loader {
  margin-top: 25vh;
  text-align: center;
}

.loader__text {
  font-weight: 700;
  font-size: 5vw;
}

